.page {
  font-family: 'Inter', sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    color: $text-color;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform $transform-transition;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin-bottom: 48px;
    text-align: center;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;

    @include onTablet {
      margin-bottom: 64px;
      font-size: 40px;
      line-height: 100%;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }
}
